import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";

export const Input = ({ type, status, label}) => {
    const classNameStatus = 
    status === 'success' ? 'input-success' :
    status === 'info' ? 'input-info' :
    status === 'alert' ? 'input-alert' :
    status === 'warning' ? 'input-warning' : null;

    return(
        <UIinput type={type} className={`input ${classNameStatus}`}>
            {label}
        </UIinput>
    )
}

Input.propTypes = {
    type: PropTypes.oneOf(['text', 'number', 'phone']),
    status: PropTypes.oneOf(['success', 'info', 'alert', 'warning']),
    label: PropTypes.string.isRequired
}

const UIinput = styled.input`
    html body input.input{
        &-success{
            border: 1px solid green;
        }
        &-info{
            border: 1px solid blue;
        }
        &-alert{
            border: 1px solid yellow;
        }
        &-warning{
            border: 1px solid red;
        }
    }
`;

